import Web3 from 'web3';
import AVAXMushroomsABI from './abi/mushrooms.json'

// testnet 
// const contractAddressMushrooms = "0xf8FbC543b78d2705215f146Dea84d627E88F2274"

// mainnet 
const contractAddressMushrooms = "0x5122f6cB13417B8C87174a2DaD3902dff3B4E4f7"

export const getWeb3 = () =>
    new Promise(async(resolve, reject) => {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            await window.ethereum.enable();
        resolve(web3);
        } catch (error) {
        reject(error);
        }
    } else if (window.web3) {
        // load metamask provider
        const web3 = window.web3;
        console.log("Injected web3 detected.");
        resolve(web3);
    } else {
         // main net
         const provider = new Web3.providers.HttpProvider("https://api.avax.network/ext/bc/C/rpc");

         // test net 
        //  const provider = new Web3.providers.HttpProvider("https://api.avax-test.network/ext/bc/C/rpc");
 
        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        resolve(web3);
    }
});
export const getInstanceMushrooms = async (web3) => {
    window.user = (await web3.eth.getAccounts())[0];
    
    window.instanceHarmonyPlanets= new web3.eth.Contract(
        AVAXMushroomsABI,
        contractAddressMushrooms,
        {
            from: window.user
        }
    );
    return window.instanceHarmonyPlanets;
}

