import React from "react";
import Grid from '@material-ui/core/Grid';

import metadata from '../metadata/_metadata.json'; 
function compareAscendingID( a, b ) {
    if ( a.edition < b.edition ){
      return -1;
    }
    if ( a.edition > b.edition ){
      return 1;
    }
    return 0;
  }
  
metadata.sort( compareAscendingID );

class PlanetWalletComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingData: true,
      tokenMetadata:[],
    };

  }
  
  componentDidMount = async () => {
    try{
        // this.setState({
        //     tokenMetadata:result.data
        // })
        let id = Number.parseInt(this.props.tokenID)
        // id+=1
        let result = metadata[id]
         this.setState({
            tokenMetadata:result
        })
       
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingData:false
      })
    }
  }


  render() {
      let id = Number.parseInt(this.props.tokenID)
      id+=1
 
     
    return(
        <div style={{margin:"auto",border:"2px solid #212529", borderRadius:"10px",overflow:"hidden"}}>
            <Grid container>
                <Grid item md={12} xs={12} style={{margin:"auto"}}>
                    <img style={{width:"100%",borderBottom:"2px solid #212529"}} src={"https://ipfs.io/ipfs/QmPSYEgaMcJxnkLFxdLK3obTh1PdsrKb4kHfg5wd3KKVvG/"+id+".png"} />
                </Grid>
            </Grid>

                    {!this.props.isMarketplace?
                     <>
                     <br />
                    <div style={{paddingLeft:"20px",paddingRight:"25px"}}>
                        <p style={{float:"left"}}> <a onClick={this.props.callBackTransfer}>Transfer token</a></p>
                    </div>
                    </>
                    : null
                    }       
           
         
            <br />
        </div>
    )
  }
}

export default PlanetWalletComponent;
