import React from "react";
import {getInstanceMushrooms,getWeb3}  from "../Web3Util";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';


import background2 from '../images/background3.jpeg'; 


import HeaderComponent from '../components/HeaderComponent';

const SKI_SUPPLY = 2000
const SKI_PRICE = 0.1

// Date and time (GMT): Tuesday, Wed Oct 20 2021 23:20:54 https://currentmillis.com/
var countDownDate = 1635271200000;

class HomePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasHomepageData: false,
      isLoadingHomepageData:true,
      nftIndexAvaxSki:-100,
      avaxSkiSelect:1,
      isMintingAvaxSki:false,
      isClaimingRewards:false,
      myRewards:0,
      totalSupply:0,
      price:0,
      timeLeft:""
    };
    this.mint = this.mint.bind(this);
    this.tick = this.tick.bind(this);
    this.decreaseSelect = this.decreaseSelect.bind(this)
    this.increaseSelect = this.increaseSelect.bind(this)
    this.claimRewards = this.claimRewards.bind(this)
    this.loadData = this.loadData.bind(this)
  }
  tick() {
   

    // Get today's date and time
    var now = new Date().getTime();
      
    // Find the distance between now and the count down date
    var distance = countDownDate - now;
      
    // Time calculations for days, hours, minutes and seconds
    var days = Math.floor(distance / (1000 * 60 * 60 * 24));
    var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    var seconds = Math.floor((distance % (1000 * 60)) / 1000);
      
    // Output the result in an element with id="demo"
   
    if (distance < 0) {
      this.setState({
        timeLeft:"Minting live!"
      })
    }else{
      this.setState({
        timeLeft:days + "D:" +hours + "H:" + minutes + "M:" +seconds+"S"
      })
    }
  
  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstanceHarmonyPlanets = await getInstanceMushrooms(window.web3);

      this.setState({ user: window.user });
      this.setState({ contractInstanceHarmonyPlanets});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
        isLoadingHomepageData:false
      })
    }
    this.interval = setInterval(() => this.tick(), 250);

  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  async loadData() {
      try {
        const totalSupply = await this.state.contractInstanceHarmonyPlanets.methods.totalSupply().call();
        this.setState({ totalSupply });
        
        this.setState({hasHomepageData:true})
    } catch (e) {
      console.log('Error', e)
    }
  }

  increaseSelect(){
    if(this.state.avaxSkiSelect<20){
      this.setState({
        avaxSkiSelect:this.state.avaxSkiSelect+1
      })
    }
  }
  
  decreaseSelect(){
    if(this.state.avaxSkiSelect>1){
      this.setState({
        avaxSkiSelect:this.state.avaxSkiSelect-1
      })
    }
  }

  async mint(){
    try {
        this.setState({isMinting:true})
        let localValue = window.web3.utils.toWei((this.state.avaxSkiSelect*SKI_PRICE)+"", 'ether')

        await this.state.contractInstanceHarmonyPlanets.methods.mint(this.state.avaxSkiSelect).send(
          {value:localValue
          }
        )
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isMinting:false})
    }
  }

  async claimRewards(){
    try {
        this.setState({isClaimingRewards:true})

        await this.state.contractInstanceHarmonyPlanets.methods.claimRewards().send()
        await this.loadData()
      } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({isClaimingRewards:false})
    }
  }
  
  render() {
    return(
       <>
          <Container>
          
          <HeaderComponent/>
          </Container>
         
          <Container style={{margin:"25px"}} >
            {window.ethereum == null || window.ethereum == undefined?
            <p style={{textAlign:"center"}}>
              <a className="install-metamask" href="https://metamask.io/download" target="_blank">Install Metamask</a>

            </p>
            :
              <>
              {window.user == null ?

                <p style={{textAlign:"center"}}>
                <a onClick={()=>{window.ethereum.enable().then(()=>window.location.reload()) }} className="install-metamask" target="_blank">Connect Metamask</a>

                </p> : null
                
              }
              
              </>
            }
          </Container>

          <Container style={{padding:"35px",paddingTop:"10vh",paddingBottom:"10vh",minHeight:"200px"}}>
            <Grid container spacing={2}>
              <Grid item md={2} xs={2}>
              </Grid>
              <Grid item md={4} xs={12}>
                <div style={{textAlign:"right"}} className="mobile-center mint">
                  <img  style={{height:"250px"}} src={"https://ipfs.io/ipfs/QmPSYEgaMcJxnkLFxdLK3obTh1PdsrKb4kHfg5wd3KKVvG/37.png"} />
                </div>
              </Grid>
              <Grid item md={4} xs={12} className="mobile-center mint minter-component">
                <div className="minted-component mobile-center mint" style={{display:"inline-block"}}>
                 {this.state.totalSupply}/{SKI_SUPPLY} <br />
                 Shrooms minted <br />
                 <div>
                   <span className="skier-select"><a  onClick={()=>{this.decreaseSelect()}}>-</a><div>{this.state.avaxSkiSelect}</div><a onClick={()=>{this.increaseSelect()}}>+</a></span>
                    <a onClick={()=>{this.mint()}}className="skier-jumpin">Mint</a>
                  </div>
                </div>
                <p className="skier-price">
                  {SKI_PRICE} $AVAX/ Mushroom
                </p>
              </Grid>
              <Grid item md={2} xs={2}>
              </Grid>
            </Grid>
          </Container>
          
          <Container  className="tokenomics-container align-items-center" style={{backgroundImage: `url(${background2})`}}>
          <Grid container spacing={2} >
              <Grid item md={12} xs={12} style={{textAlign:"center"}}>
                <div>
                  <p className="meet-planets" style={{color:"#435b5b"}}>AVAX Mushrooms</p>
                  <p style={{color:"white"}}>
                    Each mushroom is <u>100% unique</u> <br/>
                    <br/>

                    <br />
                    <h2>Rarity traits:</h2> <br />
                    <div>
                    Background <br /> <br />
                    Cyan Circular -   8% out of 100% <br />
                    Cyan -   9% out of 100% <br />
                    Green 2 -   8% out of 100% <br />
                    Green Circular -   8% out of 100% <br />
                    Green -   8% out of 100% <br />
                    Orange -   8% out of 100% <br />
                    Pink Circular -   8% out of 100% <br />
                    Purple Circular -  8% out of 100% <br />
                    Purple -   9% out of 100% <br />
                    Red -   9% out of 100% <br />
                    Yellow Circular -  9% out of 100% <br />
                    Yellow -   8% out of 100% <br />
                    <br /> <br />
                    Base <br /> <br />
                    Blue -   14% out of 100% <br />
                    Blueish -   14% out of 100% <br />
                    Cyan -   15% out of 100% <br />
                    Lime -   13% out of 100% <br />
                    Pink -   14% out of 100% <br />
                    Red -   15% out of 100% <br />
                    Yellow -   14% out of 100% <br />
                    <br /> <br />
                    Face <br /> <br />
                    Face 1 -    19% out of 100% <br />
                    Face 2 -    21% out of 100% <br />
                    Face 3 -    20% out of 100% <br />
                    Face 4 -    20% out of 100% <br />
                    Face 5 -    20% out of 100% <br />
                    <br /> <br />
                    Accessory <br /> <br />
                    Bandana Blue -  6% out of 100% <br />
                    Bandana Green -   8% out of 100% <br />
                    Bandana Red -   7% out of 100% <br />
                    Bandana Yellow -   7% out of 100% <br />
                    Crown -   6% out of 100% <br />
                    Horns Blue -   7% out of 100% <br />
                    Horns Green -   7% out of 100% <br />
                    Horns Pink -   6% out of 100% <br />
                    Horns Red -   6% out of 100% <br />
                    None -   7% out of 100% <br />
                    Special -   7% out of 100% <br />
                    Tie Blue -   6% out of 100% <br />
                    Tie Green -   6% out of 100% <br />
                    Tie Grey -   8% out of 100% <br />
                    Tie Red -   7% out of 100% <br />
                    <br/><br/>
                    
                    Insect<br /> <br />
                    Bee - 2% out of 100%<br />
                    Beetle - 5% out of 100%<br />
                    None - 93% out of 100%<br />

                    </div>
                  </p>

                  <p className="meet-planets" style={{color:"#435b5b"}}>AVAX Mushrooms</p>

                </div>
              </Grid>
            </Grid>
          </Container>
       </>
    )
  }
}

export default HomePage;
