import React from "react";
import { getInstanceMushrooms, getWeb3}  from "../Web3Util";
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Web3 from 'web3';

import PlanetWalletComponent from '../components/PlanetWalletComponent';
import HeaderComponent from '../components/HeaderComponent';


const MORE_PER_PAGE = 6

class WalletPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isClaimingRewardsMinting:false,
      myRewardsMinting:-1,
      userTokens:[],
      page:1,
      isLoadingMintingRewards:true,
      listedTokens:[],
      approvedTokens:[],
      finishedLoading:false,
      currentCounter:MORE_PER_PAGE
    };
    this.transferToken = this.transferToken.bind(this)
    this.getPlanets = this.getPlanets.bind(this)
    this.showMore = this.showMore.bind(this)
  }
  componentDidMount = async () => {
    try{
      const web3 = await getWeb3();
      window.web3 = web3;
      const contractInstanceMushrooms = await getInstanceMushrooms(window.web3);
      
      this.setState({ user: window.user });
      this.setState({ contractInstanceMushrooms});
      await this.loadData();
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({
      })
    }
  }

  async loadData() {
    if(window.user == undefined){
      this.setState({
        isLoadingMintingRewards:false
      })
      return
    }
    try {
      let userTokens = []
      let myRewardsMinting = 0

      let toAddr = window.user;
      
      console.log("@@@ here1")
      let blocknumber = await window.web3.eth.getBlockNumber()
    
      const [incomingTokenTransferEvents, outgoingTokenTransferEvents] = await Promise.all([
        this.state.contractInstanceMushrooms.getPastEvents('Transfer', { filter: {'to': toAddr}, fromBlock: blocknumber-100000, toBlock: 'latest'}), 
        this.state.contractInstanceMushrooms.getPastEvents('Transfer', { filter: {'from': toAddr}, fromBlock:  blocknumber-100000, toBlock: 'latest'}),
      ])
     
      console.log("@@@ incomingTokenTransferEvents",incomingTokenTransferEvents)

  
      incomingTokenTransferEvents.forEach( (event) =>{
        userTokens.push(event.returnValues.tokenId)
      });
     
      outgoingTokenTransferEvents.forEach( (event) =>{
        const index = userTokens.indexOf(event.returnValues.tokenId);
        if (index > -1) {
          userTokens.splice(index, 1);
        }
      });

      console.log("@@@ userTokens",userTokens)

      var collator = new Intl.Collator(undefined, {numeric: true, sensitivity: 'base'});

      userTokens.sort(collator.compare)

      this.setState({
        userTokens
      })

    } catch (e) {
      console.log('Error', e)
    }
  }

  async removeListing(skierID){
    this.setState({finishedLoading:false})

    try {
      await this.state.contractInstanceMarketplace.methods.cancelListing(skierID).send()
      await this.loadData()
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})
    }
  }

  async listToken(skierID){
    var price = prompt("Enter planet price in $AVAX");
    if(isNaN(price)){
      return
    }
    this.setState({finishedLoading:false})

    try {
      await this.state.contractInstanceMarketplace.methods.createListing(skierID,
        Web3.utils.toWei(price)
        ).send()
        await this.loadData()
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})
    }
  }

  async transferToken(skierID){
    var address = prompt("Enter address to recieve the token");
 
    this.setState({finishedLoading:false})

    try {
      await this.state.contractInstanceMushrooms.methods.transferFrom(window.user,address,skierID).send()
        await this.loadData()
    } catch (e) {
      console.log('Error', e)
    }finally{
      this.setState({finishedLoading:true})
    }
  }



  getPlanets(){
    let components = []
    let localCounter =0
    this.state.userTokens.forEach((userToken)=> {
        if(localCounter< this.state.currentCounter){
          let isApproved = false
          let skierDetails ={
            price:-1
          }
          this.state.userTokens.forEach((listedToken)=>{
            if(userToken == Number.parseInt(listedToken.tokenId)){
              skierDetails = listedToken
            }
          })
          isApproved = true
          components.push(
            <Grid key={userToken} item md={4} xs={12} style={{textAlign:"center"}} className="ski-grid">
              <PlanetWalletComponent 
              isApproved={isApproved}
              callBackApprove={()=>this.approveToken(userToken)} 
              callBackList={()=>this.listToken(userToken)} 
              callBackRemove={()=>this.removeListing(userToken)} 
              callBackTransfer={()=>this.transferToken(userToken)}
              price={skierDetails.price} 
              isMarketplace={false} 
              tokenID={userToken} 
              finishedLoading={this.state.finishedLoading}/>
            </Grid>
          )
         
        }
        localCounter++

    })
    return components
  }

  


  showMore(){
    let currentCounter = this.state.currentCounter
    currentCounter+=MORE_PER_PAGE
    this.setState({
      currentCounter
    })
  }
  
  render() {
    return(
       <>
       <Container>
          <HeaderComponent/>

          </Container>
          <Container style={{margin:"25px"}}>
            <Grid container spacing={2} >
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
                <h1 className="my-skiers-title">My Mushrooms</h1>
                <h2 className="minted-skiers">View your mushrooms!</h2>
              </Grid>
              <Grid item md={6} xs={6} style={{textAlign:"center"}}>
               
              </Grid>
            </Grid>
            <Grid container spacing={4}>
            
              {this.getPlanets()}
            </Grid>

            {this.state.currentCounter< this.state.userTokens.length ? 
              <>
              <Grid item md={12} xs={12} style={{textAlign:"center"}}>
                <br />
                  <a onClick={()=>this.showMore()} style={{textAlign:"center"}}>Click here to show more</a>
              </Grid>
              </>
            : null}
           
          </Container>
         
       </>
    )
  }
}

export default WalletPage;
